<script setup>
import { defineProps, ref, reactive } from "vue";
import TheFooter from "./TheFooter.vue";
import AppContest from "./AppContest.vue";
import AppTimer from "./AppTimer.vue";

let updated = ref(null);

const state = reactive({
  contestData: []
});

const props = defineProps({
  electionName: String,
  fileURL: String,
});

function refreshData(refreshData) {
  state.requestData = refreshData;
  loadData(state.requestData);
  state.requestData = false;
}

function loadData() {
  let updateData = [];
  fetch(window.APP_URL + "/data/" + props.fileURL + ".json")
    .then((res) => res.json())
    .then((data) => {
      if (updated.value === data.last_updated) {
        console.log("no update yet");
      } else {
        updated.value = data.last_updated;
        data.contests.forEach((el) => {
          updateData.push(el);
        });
        state.contestData = updateData;
      }
    });
}

loadData();
</script>

<template>
  <main>
    <div class="container-fluid">
      <div class="row header">
        <div class="col-sm-12 col-md-6">
          <h1 class="contest-type">{{ props.electionName }}</h1>
        </div>
        <div class="col-sm-12 col-md-6">
          <h3 class="last-updated"><small>updated: </small>{{ updated }}</h3>
          <h3 class="refresh">data refresh: <AppTimer v-on:update="refreshData($event)" /> minutes</h3>
        </div>
      </div>
      <div class="row">
        <div class="col-sm-12 col-md-6 col-xl-6" v-for="(contest, idx) in state.contestData" :key="idx">
          <AppContest :contest="contest" />
        </div>
      </div>
      <TheFooter :year="2020" :election_type="'general_election'" />
    </div>
  </main>
</template>

<style scoped>
.last-updated {
  font-size: 1.3rem;
  text-align: right;
}

small {
  font-size: 0.8rem;
  padding-right: 5px;
  font-weight: 500;
}

.refresh {
  font-size: 0.8rem;
  text-align: right;
}

.contest-type {
  text-align: left;
  height: 30px;
  line-height: 30px;
  text-transform: capitalize;
}

@media (max-width: 765px) {
  .last-updated {
    text-align: left;
  }
  .refresh {
    text-align: left;
  }
}
</style>