<script setup>
import { reactive, defineEmits, computed } from 'vue'
import { format, addSeconds } from "date-fns";

const state = reactive({seconds: 300});
const emit = defineEmits(['update'])

setInterval(() => {
  if (state.seconds === 0) {
    state.seconds = 0
    emit("update");
    state.seconds = 300            
  } else {
    state.seconds -= 1
  }             
}, 1000)

const timer = computed(() => {
    var tempDate = addSeconds(new Date(0), state.seconds);
    return format(tempDate, "m:ss");
})

</script>

<template>
  <span>{{ timer }}</span>
</template>

