import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/state-senate/',
    name: 'Senate',
    component: () => import(/* webpackChunkName: "senate" */ '../views/Senate.vue')
  },
  {
    path: '/state-represenatives/',
    name: 'House',
    component: () => import(/* webpackChunkName: "house" */ '../views/House.vue')
  },
  {
    path: '/city/',
    name: 'Local',
    component: () => import(/* webpackChunkName: "local" */ '../views/Local.vue')
  },
  {
    path: '/city/:id/',
    name: 'LocalContests',
    component: () => import(/* webpackChunkName: "local-details" */ '../views/LocalContests.vue'),
    props: true
  },
  {
    path: '/custom/',
    name: 'Custom',
    component: () => import(/* webpackChunkName: "custom" */ '../views/Custom.vue')
  },
  {
    path: '/about/',
    name: 'About',
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  {
    path: '/changelog/',
    name: 'Changelog',
    component: () => import(/* webpackChunkName: "changelog" */ '../views/Changelog.vue')
  },
  {
    path: '/test/',
    name: 'Test',
    component: () => import(/* webpackChunkName: "changelog" */ '../views/Test.vue')
  },
  {
    path: '/test2/',
    name: 'Test2',
    component: () => import(/* webpackChunkName: "changelog" */ '../views/Test2.vue')
  },
  {
    // redirect to home if page doesn't exist
    path: '/:catchAll(.*)',
    redirect: '/'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
